body {
  margin: 0;
  padding: 0;
  font-family: "\B9D1\C740   \ACE0\B515", Malgun Gothic;
  color: #000;
  transition: all 0.4s ease;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a:link {
  color: #000;
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  border: 0 none;
  padding: 0;
  margin: 0;
  font-size: 100%;
  color: #000;
  vertical-align: middle;
  background: transparent;
  cursor: pointer;
}
button::-moz-focus-inner {
  margin: -1px;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

textarea {
  overflow: auto;
  width: 100%;
  padding: 0;
  font-size: 100%;
  border: none;
  border-radius: 0;
  resize: none;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
}
textarea[disabled],
textarea[readonly] {
  background-color: #ccc;
}

.list {
  clear: left;
  overflow: hidden;
  margin-left: -2.33%; }
  .list li {
    float: left;
    width: 31%;
    margin: 40px 0 0 2.33%;
    padding: 20px;
    border-top: 1px solid #999; }
    .list li a {
      display: block; }
      .list li a h3 {
        font-size: 1.05rem;
        color: #6f3685; }
      .list li a .flex {
        position: relative;
        margin-top: 2px;
        padding-right: 70px;
        font-size: 0.9rem;
        color: #777; }
        .list li a .flex .left {
          margin-right: auto; }
        .list li a .flex .right {
          position: absolute;
          right: 0;
          top: 0; }
      .list li a:hover h3 span {
        border-bottom: 1px solid #6f3685; }
    .list li .areas {
      margin-top: 30px; }
      .list li .areas p {
        font-size: 2rem;
        line-height: 3rem; }

@media all and (max-width: 900px) {
  .list {
    margin-left: -2%; }
    .list li {
      width: 48%;
      margin-left: 2%; } }

@media all and (max-width: 600px) {
  .list {
    margin-left: 0%; }
    .list li {
      width: 100%;
      margin-left: 0; } }

